import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    jobs: [],
    displayJobs: [],
    rows: 0
  },
  mutations: {
    SET_JOBS(state, jobs) {
      state.jobs = jobs;
    },
    SET_ROWS(state, rows) {
      state.rows = rows;
    },
    SET_DISPLAY_JOBS(state, displayJobs) {
      state.displayJobs = displayJobs;
    }
  },
  actions: {
    fetchData() {
      // The place where you do the async calls to backend
      return new Promise(resolve => {
        setTimeout(async () => {
          const res = await fetch("jobs.json");
          const val = await res.json();
          resolve(val);
        });
      });
    },
    async fetchJobs({dispatch, commit}) {
      const myJson = await dispatch("fetchData");
      commit("SET_JOBS", myJson);
      commit("SET_ROWS", myJson.length);
      const displayJobs = myJson.slice(0, 3);
      commit("SET_DISPLAY_JOBS", displayJobs);
      commit("SET_ROWS", myJson.length);
    },
    async paginate({commit, state}, {currentPage, perPage}) {
      const start = (currentPage - 1) * perPage;
      const jobs = state.jobs.slice(start, start + perPage);
      commit("SET_DISPLAY_JOBS", jobs);
    },
    updatePagination({commit, dispatch}, {myJson, currentPage, perPage}) {
      commit("SET_JOBS", myJson);
      commit("SET_ROWS", myJson.length);
      dispatch("paginate", {currentPage, perPage})
    },
    async search({dispatch}, {text}) {
      const myJson = await dispatch("fetchData");  // To get the most recent from server
      const values = myJson.filter(val => val.name.toLowerCase().includes(text.toLowerCase()));
      dispatch("updatePagination", {
        myJson: values,
        currentPage: 1,
        perPage: 3
      })
    }
  },
  getters: {
    jobs(state) {
      return state.jobs;
    },
    rows(state) {
      return state.rows;
    },
    displayJobs(state) {
      return state.displayJobs;
    }
  },
  modules: {}
});
